* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

body {
  background-color: #053b50;
  color: #333;
  line-height: 1;
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  height: 80vh;
  margin: 20px auto;
  background-color: #176b87;
  text-align: center;
  padding: 20px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
}

.list h2 {
  font-size: 50px;
  margin: 50px;
  color: #eeeeee;
}

.list form {
  width: 300px;
  height: 50px;
  margin: 0 auto 30px;
  position: relative;
}
.list form input {
  border: none;
  width: 300px;
  height: 50px;
  padding: 20px;
  border-radius: 15px;
}
.list form button {
  border: none;
  background-color: #64ccc5;
  color: #333;
  line-height: 25px;
  border-radius: 5px;
  height: 25px;
  width: 40px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.list ul {
  color: #eeeeee;
  width: 80%;
  border: 2px solid #eeeeee;
  border-radius: 20px;
  margin: 0 auto;
  overflow: hidden;
  height: 480px;
  padding: 10px;
}
